<template>
  <div id="app" v-if="unlock">
    <img
      class="cover"
      :src="`${publicPath}/imgs/${data.cover}`"
      alt=""
      @click="makeMagic"
    />
    <div class="con">
      <h2>{{ data.title }}</h2>
      <h4
        v-for="(item, index) in data.desps"
        :key="item"
        :class="{ 'last-desp': index === data.desps.length - 1 }"
      >
        {{ item }}
      </h4>
      <Card v-for="(card, index) in data.list" :key="index" :card="card"></Card>
      <Cover v-if="showMagic" :index="index"></Cover>
    </div>
    <MusicBar></MusicBar>
  </div>
  <Lock v-else @checked="handleChecked"></Lock>
</template>

<script>
import Card from "./components/Card.vue";
import Cover from "./components/Cover.vue";
import Lock from "./components/Lock.vue";
import MusicBar from "./components/MusicBar.vue";
import data from "./config/data.js";

export default {
  name: "App",
  components: {
    Card,
    Cover,
    Lock,
    MusicBar,
  },
  data() {
    window.vm = this;
    return {
      data,
      showMagic: false,
      unlock: window.localStorage.getItem("__UNLOCK__") || false,
      index: 0,
      // unlock: false
    };
  },
  computed: {
    publicPath() {
      return process.env.NODE_ENV === "production"
        ? "/timeline/love/assets"
        : "/assets";
    },
  },
  methods: {
    makeMagic() {
      // this.showMagic = true;
      setTimeout(() => {
        this.reverse();
      }, 0);
      // setTimeout(() => {
      //   this.reverse();
      //   setTimeout(() => {
      //     this.showMagic = false;
      //     if (this.index < data.sp.length - 1) {
      //       this.index++;
      //     }
      //   }, 2000);
      // }, data.sp[this.index].messages.length * 2000);
    },
    reverse() {
      this.data.list = [...this.data.list.reverse()];
    },
    handleChecked() {
      window.localStorage.setItem("__UNLOCK__", "YES");
      this.unlock = true;
      this.$nextTick(() => {
        document.scrollingElement.scrollTop = 0;
      });
    },
  },
};
</script>

