<template>
  <div class="card">
    <div class="card-tail"></div>
    <div class="card-head"></div>
    <div class="card-content">
      <h3 class="date">{{ card.date }}</h3>
      <p
        v-for="(item, index) in card.messages"
        :key="index"
        :class="{ 'has-avatar': isYuan(item) || isXiao(item) }"
      >
        <img
          :src="`${publicPath}/imgs/yuanyuan.jpeg`"
          alt=""
          class="avatar"
          v-if="isYuan(item)"
        />
        <img
          :src="`${publicPath}/imgs/xiao.jpeg`"
          alt=""
          class="avatar"
          v-if="isXiao(item)"
        />
        <span v-if="isXiao(item) || isYuan(item)">{{
          item.replace(/^鸢鸢：/, "").replace(/^箫竹：/, "")
        }}</span>
        <span v-else class="blod">{{ item }}</span>
      </p>
      <h3 v-if="card.imgAlt">{{ card.imgAlt }}</h3>
      <img
        v-for="item in card.imgs"
        :key="item"
        :src="`${publicPath}/imgs/${item}`"
        :alt="card.imgAlt || ''"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "Card",
  props: {
    card: Object,
  },
  computed: {
    publicPath() {
      return process.env.NODE_ENV === "production"
        ? "/timeline/love/assets"
        : "/assets";
    },
  },
  methods: {
    isYuan(message) {
      const r = /^鸢鸢：/;
      return r.test(message);
    },
    isXiao(message) {
      const r = /^箫竹：/;
      return r.test(message);
    },
  },
  data() {
    return {};
  },
};
</script>

