<template>
  <div class="music-bar">
    <audio ref="audio">
      <source :src="`${publicPath}/${music}`" type="audio/mpeg" />
    </audio>
    <img
      :src="`${publicPath}/music.png`"
      class="musicIcon"
      :class="[move ? 'move' : '', 'musicIcon']"
      @click="toggle"
    />
  </div>
</template>

<script>
import data from "../config/data.js";

export default {
  data() {
    return {
      music: data.music[0].url,
      move: false,
    };
  },
  computed: {
    publicPath() {
      return process.env.NODE_ENV === "production"
        ? "/timeline/love/assets"
        : "/assets";
    },
  },
  methods: {
    toggle() {
      !this.move ? this.$refs.audio?.play?.() : this.$refs.audio.pause();
      this.move = !this.move;
      console.log(this.$refs.audio.play, this.$refs.audio.pause);
    },
  },
};
</script>

<style>
.music-bar {
  /* position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 60px;
  background: rgba(0, 0, 0, 0.8); */
}
.musicIcon {
  position: fixed;
  top: 20px;
  right: 20px;
  width: 20px;
  height: 20px;
  animation-fill-mode: forwards;
}
.musicIcon.move {
  animation: rotate 4s infinite;
  animation-timing-function: linear;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>
