<template>
  <div class="mask">
    <div class="mask-con">
      <p>请输入口令</p>
      <!-- <p v-if='checked'>待小七施展魔法</p> -->
      <input
        placeholder=""
        class="token"
        v-model="token"
        type="text"
        @input="check"
        @keydown.enter="check"
      />
    </div>
  </div>
</template>

<script>
import data from "../config/data.js";

export default {
  data() {
    return {
      token: "",
      checked: false,
    };
  },
  methods: {
    check() {
      if (btoa(encodeURIComponent(String(this.token))) === data.token) {
        this.checked = true;
        this.$emit("checked");
      }
    },
  },
};
</script>

<style>
.token {
  color: #fff;
  background: transparent;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.9);
  outline: none;
  font-size: 20px;
  height: 40px;
  width: 60vw;
  display: block;
  margin: 20px auto 0;
  border-radius: 0;
  text-align: center;
}
</style>
